.sideBar {
    display: flex;
}

.sideBarBlock {
    padding: 0 70px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h3 {
    font-size: 30px;
    font-weight: 900;
    letter-spacing: 4px;
}

.navBlock {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: auto;
}

.mainText {
    text-decoration: none;
    letter-spacing: 1.1px;
}

@media screen and (max-width: 768px) {
    .sideBar {
        display: none;
    }
}
