.skills {
    background-color: #f9f9f9;
}

.skillsSection {
    display: flex;
    flex-wrap: wrap;
    min-height: 300px;
    gap: 85px;
}

.title {
    display: inline-block;
    background-color: rgba(0, 0, 0, .04);
    padding: 4px 10px;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 14px;
    color: #575555;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 0;
}

h2 {
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.4;
    color: #000;
    font-size: 30px;
    margin: 11px 0 65px 0;
}

@media screen and (max-width: 768px) {
    .skillsSection {
        gap: 30px;
        justify-content: center;
        padding: 0 30px;
    }
}